footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 15vh;
  background: #231f20;
  color: white;
}

/* css for the homepage buttons */

.btn-container {
  display: flex;
  justify-content: space-between;
  width: 40%;
  margin-left:350px;
  margin-right: 40px;
  padding-top: 100px;

}

.btn-primary {
  width: 40%;
  height: 30px;
  background-color: #4AA3FB;
  color: white;
  border-radius: 5px;
}

.btn-container .btn:first-child {
  margin-right: 60px;
  padding: 20px 40px;
  font-size: 20px;


}

.btn-container .btn:last-child {
  margin-left: 50px;
  padding: 20px 40px;
  font-size: 20px;
}




