.Selections{
  margin-top: 19px;
  padding-top: 50px;
  background-color: #F7F8FA;
  height: 600px;
  width: 180px;
  opacity: 0.9;
  box-shadow: 0px 4px 4px #828F9C, 0px 4px 4px #828F9C;
  /* filter: blur(2px); */
